
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Search from './panel/Search'
import AppTitle from './bar/AppTitle'
import TitleBar from './bar/Title'
import BottomBar from './bar/Bottom'
import localforage from 'localforage'
import {post,globalData} from "./method.js" 

//
const $tiangongyuanyuan = require('../assets/tiangongyuanyuan.png'),
      $xinxifabu        = require('../assets/xinxifabu.png'),
      $youjian          = require('../assets/youjian.png'),
      $bangongxitong    = require('../assets/bangongxitong.png'),
      $1                = require('../assets/1.png'),
      $2                = require('../assets/2.png');
export default {
  data () {
    return {
      index: 0,
      appList:{},
      showList:[
        {url: 'https://translate.google.cn/',img: $1,title: ''},
        {url: 'https://translate.google.cn/', img: $2, title: ''}
      ],
      textAlert:'',//
      showPositionValue:false,
      showDelateButton:false,//
      idCard:0
    }
  },
  created(){
    const _this = this;
    //
    //---------------------------------------------------------
    //
    if(globalData.showList === null){
      post("http://localhost:9999/appRequest",{type:5},function(d){
        if(d !=="" && d !==null){
          const Data = JSON.parse(d);
          _this.showList = Data
          globalData.showList = Data
        }
        else{
          _this.textAlert = ''
          _this.showPositionValue = true
        }
      });
    }
    else{ //
      _this.showList = globalData.showList
    }
    //---------------------------------------------------------

    //
    localforage.getItem('appList', function (err, value) {
      //
      if(err !== null || value === "" ||value === null){
        _this.appList = {
          tiangongyuanyuan:{id:"10000", name:"", icon:$tiangongyuanyuan,url:'#', special:"open", type:"communication", isSelect:false},
          xinxifabu:{id:"10001", name:"", icon:$xinxifabu, url:'http://info.casic.cs/jeecms2/index/mobile/', special:"url", type:"office" ,isSelect:false},
          youjian:{id:"10002", name:"", icon:$youjian,url:'', special:"url", type:"office", isSelect:false},
          bangongxitong:{id:"10004", name:"", icon:$bangongxitong,url:'', special:"url", type:"office", isSelect:false}
        }
        localforage.setItem('appList', _this.appList);
      }
      else{
        //
        _this.appList = value
      }
    });
    //
    //-------------------------------------------------------------------
    const value = globalData.userData
    _this.idCard = value.idCard
    const appList = _this.appList
    if(value.key !== 1){
      appList.bangongxitong = {}
    }
    else{
      //,URL
      if(appList.bangongxitong !== undefined){
        appList.bangongxitong.url = 'http://10.152.36.26:8080/portal/menu.jsp?userName='+value.userName+'&PID='+value.idCard+'&webService=&SessionID='
      }
    }
    if(appList.youjian !== undefined){
      appList.youjian.url = 'http://10.152.36.31/secmail/loginapp.do?type=cid&PID='+value.idCard
    }
    //-------------------------------------------------------------------
  },
  methods: {
    onIndexChange (index) { //
      this.index = index
    },
    openApp: function () { //
      const app1 = {
        "type":2,
        "sopid":"com.vrv.linkDood",
        "pkgpath":"com.vrv.linkDood-1.0.45.sop",
        "scheme":"linkdood:showlinkdood?id="+this.idCard,
        "name":"linkdood"
      };
      //9999Post
      post("http://localhost:9999/open",app1,function(date){
        console.log(date)
      });
    },
    openStart:function(url,special){ //
      switch(special){
        case 'open':this.openApp();break; //
        case 'url':window.location.href=url;break; //Url
      }
    },
    pressItem:function(key){ //app
      //appItem
      this.appList[key].isSelect = true
      //
      this.showDelateButton = true
    },
    delateApp:function(){
      const oldList = this.appList,
            _this   = this;
      let   newList = {},
            mark    = false ;//app
      for(let item in oldList){
        //Jsonmark
        if(!oldList[item].isSelect){ newList[item] = oldList[item] }
        else{ mark = true; }
      }
      //mark
      if(mark) {
        //
        localforage.setItem('appList', newList, function (err){
          _this.appList = newList
        });
      }
      //
      _this.showDelateButton = false
    },
    exit:function(key){
      //appItem
      this.appList[key].isSelect = false
      //
      this.showDelateButton = false
    }
  },
  components: {
    Search,
    Swiper,
    AppTitle,
    Grid,
    GridItem,
    TitleBar,
    BottomBar,
    Toast
  }
}



